@keyframes spread {
    0% {
        width: 0;
        height: 0;
        opacity: 1;
    }

    100% {
        width: 500px;
        height: 500px;
        opacity: 0;
    }
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --maincolor: #86a8e7;
    --darkcolor: #7f7fd5;
    --brightcolor: #f7f7f7;
}

.container {
    position: relative;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: linear-gradient(to right, #91eae4, #86a8e7, #7f7fd5);
    overflow: hidden;
    border: 2px solid #90D5E5;
}

.container .btn {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    border: none;
    background-color: var(--maincolor);
    box-shadow: 0px 1px 10px rgba(2 37 201/ 0.2);
    transition: transform 0.2s ease-out;
    z-index: 10;
    cursor: pointer;
}

.container .btn:hover {
    transform: scale(1.1);
}

.container .btn:active {
    transform: scale(1);
}

.container .btn::before {
    content: 'Enter';
    color: var(--brightcolor);
    font-family: 'GmarketSansMedium', cursive;
    font-weight: 400;
    font-size: 12px;
}

.container .skip{
    font-weight: bold;
    font-family: 'GmarketSansMedium', cursive;
    margin-top: 20px;
    font-size: 12px;
    background-color: var(--maincolor);
    box-shadow: 0px 1px 10px rgba(2 37 201/ 0.2);
    transition: transform 0.2s ease-out;
    cursor: pointer;
    z-index: 10;
}

.container .skip:hover {
    transform: scale(1.1);
}

.container .skip:active {
    transform: scale(1);
}

.container .wave_wrap {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container .wave_wrap .wave {
    position: absolute;
    width: 200px;
    height: 200px;
    border: 10px solid var(--brightcolor);
    border-radius: 50%;
    filter: blur(15px);
    box-shadow: inset 6px 6px 15px rgba(2 37 201/ 1),
    6px 6px 15px rgba(2 37 201/ 1),
    inset calc(6px * -1) calc(6px * -1) 15px rgba(2 37 201/ 0.2),
    calc(6px * -1) calc(6px * -1) 15px rgba(2 37 201/ 0.2);
    animation: spread;
    animation-duration: 3s;
    animation-timing-function: cubic-bezier(0, 0.2, 0.8, 1);
    animation-iteration-count: infinite;
}

.container .wave_wrap .wave:nth-child(2) {
    animation-delay: -1s;
}

.container .wave_wrap .wave:nth-child(3) {
    animation-delay: -2s;
}

@keyframes btn_fadeOut {
    from {
        opacity: 100;
    }
    to {
        opacity: 0;
        display: none;
    }
}

@keyframes video-fadeIn{
    from{
        opacity: 0;
    }
    to {
        opacity: 100
    }
}

@keyframes message-fade{
    0%{
        opacity: 0;
    }
    20% {
        opacity: 100;
    }
    80% {
        opacity: 100;
    }
    100% {
        opacity: 0;
        display: none;
    }
}

.btn_animation{
    animation: btn_fadeOut 2s linear forwards;
}

.video_animation{
    animation: video-fadeIn 2s linear forwards;
    animation-delay: 1s;
}

.message_one_animation{
    animation: message-fade 3s linear forwards;
    animation-delay: 3s;
}

.message_two_animation{
    animation: message-fade 3s linear forwards;
    animation-delay: 7s;
}

.message_three_animation{
    animation: message-fade 3s linear forwards;
    animation-delay: 11s;
}
